<script setup>
import { ref } from "vue";
let joker = ref("google-joker.png");
let jokers = [
  "apple-joker",
  "crying-joker",
  "crying-joker2",
  "cursing-joker",
  "flower-joker",
  "google-joker",
  "heart-joker",
  "letter-joker",
  "loving-joker",
  "pumpkin-joker",
  "scared-joker",
  "sleeping-joker",
  "sweating-joker",
  "turtle-joker",
];

function getNum(a, b) {
  return Math.floor(Math.random() * (b - a + 1) + a);
}
function radomJoker() {
  let s = jokers[getNum(0, jokers.length - 1)];
  joker.value = `${s}.png`;
}
